.outlook-chart-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.infotext-container {
    margin-top: 24px;
    max-width: 100%;
    text-align: center;
    font-size: 1.1rem;
    color: rgb(0, 0, 0);
    transition: all 0.3s ease-in-out;

    p {
      text-align: left;
    }
  }

  .info-navigation {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 10px;
    margin-bottom: 12px;

    .nav-button {
      background: none;
      border: none;
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 20px;
    }

    .nav-button svg {
      font-size: 24px;
    }
  }